.app-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 1rem;

}

table {  
  border-collapse: collapse;
  width: 90%;
}

th,
td {
  border: 2px solid #0a0909;
  text-align: left;
  padding: 8px;
  font-size: 22px;
}

th {
  background-color: rgb(170, 144, 40);
  font-family: cursive;
  color: black;
}

td {
  background-color:rgb(90, 87, 87);
  font-family: cursive;
  color: white;
}

form {
  display: flex;
  gap: 5px;
}

form td:last-child {
  display: flex;
  justify-content: space-evenly;
}

form * {
  font-size: 18px;
}
h2{
    color: rgb(193, 127, 40);
    font-size: 27px;
    font-family: cursive;
}